
.overlay {
  position: absolute;
  left: 0;
  top: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1;
  width: 100%;
  height: 100%;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease-in-out,visibility 0.3s ease-in-out;
  overflow: hidden;
}
.overlayVisible {
  visibility: visible;
  opacity: 1;
  .drawer{
  transform: translateX(0);

  }
}
.drawer {
  position: absolute;
  width: 420px;
  height: 100%;
  right: 0;
  background: #ffffff;
  box-shadow: -10px 4px 24px rgba(0, 0, 0, 0.1);
  padding: 30px;
  display: flex;
  flex-direction: column;
  transform: translateX(100%);
  transition: transform 0.3s ease-in-out;

  h2 {
    display: flex;
    justify-content: space-between;
    margin: 0;
    font-size: 24px;
    img {
      cursor: pointer;
      opacity: 0.5;
      &:hover {
        opacity: 1;
      }
    }
  }
}
